.additions-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  background-color: #FFFFFF;
  padding: 23px 18px 22px 18px;
  text-transform: uppercase;
}

.additions-item__title {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-align: left;
  margin: 0;
  padding: 0;
  padding-left: 47px;
  margin-bottom: 16px;
}

.additions-item__description {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-align: left;
  margin: 0;
  padding: 0;
  padding-left: 47px;
  margin-bottom: 30px;
}
